import type { ComponentProps } from 'react';
import styled from 'styled-components';
import Qubic from '@ardata-tech/qubic-js';
import { ReactComponent as wall } from '../assets/qubic_bg.svg';
import { WalletDetailsSection, TransactionSection } from '../components';
import { defaultSnapOrigin } from '../config';
import {
  useMetaMask,
  useInvokeSnap,
  useMetaMaskContext,
  useRequestSnap,
} from '../hooks';
import { isLocalSnap, shouldDisplayReconnectButton } from '../utils';

const WalletContainer = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../assets/qubic_bg.svg"); /* Adding the image */
  background-size: cover; /* Ensure the image covers the div */
  background-position: center; /* Center the image */
`;

const Index = () => {
  const { error } = useMetaMaskContext();
  const { isFlask, snapsDetected, installedSnap } = useMetaMask();
  const requestSnap = useRequestSnap();
  const invokeSnap = useInvokeSnap();

  const qubic = new Qubic({
    providerUrl: 'https://rpc.qubic.org',
    version: 1,
  });

  const isMetaMaskReady = isLocalSnap(defaultSnapOrigin)
    ? isFlask
    : snapsDetected;

  const handleSendHelloClick = async () => {
    await invokeSnap({ method: 'hello' });
  };

  const sendTransaction = async () => {
    const jsonString: any = await invokeSnap({ method: 'getPublicId' });
    console.log('handleSendHiClick', jsonString);

    const privateKey = JSON.parse(jsonString).privateKey;
    console.log('handleSendHiClick privateKey', privateKey);

    const privateKeyBase26 = qubic.utils.hexToBase26(privateKey);
    console.log('handleSendHiClick privateKeyBase26', privateKeyBase26);

    const identity = await qubic.identity.createIdentity(privateKeyBase26);

    console.log('identity', {
      publicKey: identity.publicKey,
      privateKey: identity.privateKey,
      publicId: identity.publicId,
    });

    const to = 'WIJBIFGHSKDFBFRKLUHZTAIVYMJDNJMKUBBMULXBUCRCVQFCZTJRNQXDOSDN';
    const amount = 100;
    const tick: any = await qubic.chain.getLatestTick();
    const transactionData = await qubic.transaction.createTransaction(
      identity.publicId,
      to,
      amount,
      tick + 10,
    );

    console.log('transactionData', transactionData);

    const signedTransaction = await qubic.transaction.signTransaction(
      transactionData,
      identity.privateKey,
    );

    console.log('signedTransaction', signedTransaction);

    const signedTransactionBase64 = btoa(
      String.fromCharCode(...signedTransaction),
    );

    console.log('signedTransactionBase64', signedTransactionBase64);

    const result = await qubic.transaction.broadcastTransaction(
      signedTransactionBase64,
    );

    console.log('result', result);
  };

  // <ReconnectButton onClick={requestSnap} disabled={!installedSnap} />;

  const Header = styled.header`
    display: flex;
    width: 80%;
    height: 75px;
    top: 150px;
    left: 155px;
    justify-content: space-between;
  `;

  return (
    <WalletContainer>
      <Header>
        <div>test</div>
        <div>button</div>
      </Header>
      <WalletDetailsSection address="0x..." balance={0} />
      <TransactionSection
        amountValue={0}
        destinationValue="0x..."
        tickValue={0}
      />
    </WalletContainer>
  );
};

export default Index;
